.gpt3__whatgpt3    
{
    /* ff 3.6+ */
    background: -moz-radial-gradient(circle at 50% 50%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 100%);

    /* safari 5.1+,chrome 10+ */
    background: -webkit-radial-gradient(circle at 50% 50%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 100%);

    /* opera 11.10+ */
    background: -o-radial-gradient(circle at 50% 50%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 100%);

    /* ie 10+ */
    background: -ms-radial-gradient(circle at 50% 50%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 100%);

    /* global 92%+ browsers support */
    background: radial-gradient(circle at 50% 50%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 100%);
}
gpt3__whatgpt3 -feature{
    display:flex;
}
.gpt3__whatgpt3-feature ,
 .gpt3__whatgpt3-feature-container{
    margin:0;
}

.gpt3__whatgpt3-feature ,
 .gpt3__whatgpt3-feature-container-text{
    margin:0;
} 

.gpt3__whatgpt3-heading{
    display:flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: row;
    margin: 4rem 0 2rem;
}
.gpt3__whatgpt3-heading p{
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px; 
    line-height: 30px;
    max-width: 510px;
    color:var(--color-subtext);
    cursor:pointer;
    
}

.gpt3__whatgpt3-feature-container{
    display :flex;
    flex-wrap: wrap;
    flex-direction: row;
    margin-top: 2rem;
}


.gpt3__whatgpt3-container .gpt3__features-container__feature {
    flex: 1;
    margin: 1rem;
    min-width: 210px;
    display: unset;
    flex-direction: column;
}

/* Customizing feature component as per needs */
.gpt3__whatgpt3-container .gpt3__features-container_feature-text {
    margin-top: 0.5rem;
}

@media screen and (max-width: 850px) {
    .gpt3__whatgpt3-heading {
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 0rem;
    }

    .gpt3__whatgpt3-heading p {
        margin-top: 1rem;
    }
}

@media screen and (max-width: 650px) {
    /* Customizing feature component as per needs */
    .gpt3__whatgpt3-feature .gpt3__features-container__feature {
        flex-direction: column;
    }

    /* Customizing feature component as per needs */
    .gpt3__whatgpt3-feature .gpt3__features-container_feature-text {
        margin-top: 0.5rem;
    }

    .gpt3__whatgpt3-heading h1 {
        font-size: 28px;
        line-height: 40px;
    }
}

@media screen and (max-width: 350px) {
    /* Customizing feature component as per needs */
    .gpt3__whatgpt3-container .gpt3__features-container__feature {
        margin: 1rem 0;
        min-width: 100%;
    }
}