.gpt3__features-container-feature{
    width:100%;
    display:flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;
    margin:1rem;
}
.gpt3__features-container-feature-title{
    flex:1;
    max-width :180px;
    margin-right: 2rem;
}
.gpt3__features-container-feature-title h1{
    font-family:var(--font-family);
    font-weight: 800;
    font-size: 18px;
    line-height: 25px;
    letter-spacing: -0.04em;
    color:#FFFFFF;
}
.gpt3__features-container-feature-title div{
    width: 38px;
    height:3px;
    left: 192px;
    top:1488px;
    background: var(--gradient-bar);
box-shadow: 0 4px 4px rgba(0.0.0.0.25em);
margin-bottom: 0.25rem;
}
.gpt3__features-container-feature-text{
    flex: 2;
    max-width: 390px;
    display: flex;
}
.gpt3__features-container-feature-text p{
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color:var(--color-text)
}
@media screen  and(max-width:550px) {
    .gpt3__features-container-feature-title h1{
        font-size:14x ;
        line-height: 22px;
    }
    .gpt3__features-container-feature-title p{
        font-size:12x ;
        line-height: 20px;
    }
    .gpt3__features-container-feature{
        margin:1rem 0;
    }
}