.gpt__navbar{
    padding:2rem 6rem;
    display:flex;
    justify-content: space-between;
    align-items: center;

}
.head{
    margin-left: 230px;
}
.gpt3__navbar-links{
    flex:1;
    align-items: center;
    justify-content: flex-start;
    display: flex;
}
.gpt3__navbar-links-logo{
    margin-right: 2rem;

}
.gpt3__navbar-links-logo img{
    width: 92.56px;
    height: 96.02px;
    
}
.gpt3__navbar-links_container{
    display: flex;
}
.gpt3__navbar-sign{
    display:flex;
    justify-content: flex-end;
    align-items: center;
}
.gpt3__navbar-links_container p,
.gpt3__navbar-sign p,
.gpt3__navbar-menu_container p{
    color:#fff;
    font-family: var(--font-family--);
    font-size: 19px;
    font-weight: 500;
    line-height: 25px;
    cursor:pointer;
    text-transform: capitalize;
    margin:0 1rem;

}
.gpt3__navbar-sign button,
.gpt3__navbar-menu-container button,
.gpt3__navbar-menu_container-sign button
{
    font-family: var(--font-family--);
    font-weight: 500;
border-radius: 5px;
padding: 0.5rem 1rem;
top:55px;
left:1153px;
background: #FF4820;
color:#fff;
line-height: 25px;
border:0;
outline:none;
cursor:pointer;
}
.gpt3__navbar-menu{
    margin-left: 1rem;
    display:none;
    position:relative;

}
.gpt3__navbar-menu svg{
    cursor: pointer;
}
.gpt3__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    text-align: end;
    background: var(--color-footer);
    padding:2rem;
    position:absolute;
    top:0px;
    right:0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0,0,0,0.2);
}
.gpt3__navbar-menu_container p{
    margin:1rem 0;

}
.gpt3__navbar-menu_container-sign{
    display:none;


}

@media screen and (max-width: 1050px) {
    .gpt3__navbar-links_container{
display: none;
    }
    .gpt3__navbar-menu {
        display:flex;
    }
}


@media screen and (max-width: 750px) {
    .gpt3__navbar{
       padding: 2rem 4rem;
    }
    
}

@media screen and (max-width: 550px) {
    .gpt3__navbar{
       padding: 2rem ;
    }
    .gpt3__navbar-sign{
        display: none;
    }
    .gpt3__navbar-menu-container{
        top:20px
    }
    .gpt3__navbar-menu_container-sign{
        display: block;
    }
    
}

